#root {
  position: absolute;
  width: 100%;
  height: 100%;
  font-family: Trebuchet, sans-serif;
  background: rgb(250, 230, 200);
  padding: 0;
  margin: 0;
}

body {
  padding: 0;
  margin: 0;
}

#wrapper {
  display: flex;
  flex-direction: row;
  align-content: center;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
}

#board {
  padding: 32px;
  border: 5px solid black;
  width: 50%;
  height: 80%;
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
}
table {
  padding: 5;
  border: none;
  flex-basis: auto;
}
tr {
  padding: none;
  margin: 0;
}
img {
  display: block;
  margin: none;
  padding: none;
  width: 100%;
  height: 100%;
}

p {
  color: red;
}

td {
  aspect-ratio: 1 / 1;
}
td.grid {
  border: 1px dotted gray;
  border-collapse: separate;
  padding: 0;
  margin: 0;
  padding-bottom: 0px;
}

td.out {
  border-collapse: separate;
  padding: 0;
  padding-bottom: 0px;
}



table, tbody {
  border-collapse: collapse;
  padding: 0;
  margin: 0;
  padding-bottom: 0px;
}

#bottom {
  width: 20%;
  height: 80%;
  display: flex;
  text-align: center;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  padding: 32px;
  border: 5px solid black;
  margin: 16px;
}

#tiles {
  margin-top: 20%;
  padding: 4px;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-content: center;
  justify-content: center;
  border: 2px solid black;
}

#tiles img {
  position: relative;
  aspect-ratio: 1 / 1;
  width: 64px;
  height: 64px;
}

.tile {
  position: relative;
  padding: 5%;
  text-align: right;
}

.tile p {
  color: black;
  font-size: 28px;
  aspect-ratio: 1 / 1;
  position: absolute;
  top: 0%;
  right: 0%;
  padding: 0;
  margin: 0;
  font-weight: 600;
}

button {
  height: 64px;
  background: gray;
  border: 2px solid black;
  margin: 4px;
  border: none;
}
